* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  overflow-wrap: break-word;
}

:lang(ko) {
  word-break: keep-all;
}

a {
  color: inherit;
  text-decoration: none;
}

input,
select,
textarea,
button {
  appearance: none;
  border: 0;
  background: transparent;
  color: inherit;
  font-size: inherit;
}

button {
  cursor: pointer;
}

ul,
ol {
  list-style: none;
}

img {
  max-width: 100%;
  height: auto;
}
