@use '../utils/untils' as *;

.page {
  @include position(relative);
  @include grid($justify: center);
  @include grid-cols(1fr);

  @include media-tablet {
    @include grid-cols(88px minmax(auto, 600px));
  }

  @include media-laptop {
    @include grid-cols(88px 600px 320px);
  }

  @include media-desktop {
    @include grid-cols(275px 600px 390px);
  }

  &__header {
    @include position(relative);
  }

  &__mobile-title {
    @include border(orange);
    @include position(fixed, $top: 0);
    @include padding($right: 16px, $left: 16px);
    @include size($w: 100%, $h: 52px);

    @include media-tablet {
      @include hidden;
    }
  }

  &__mobile-navbar {
    @include border(orange);
    @include position(fixed, $bottom: 0);
    @include padding($right: 16px, $left: 16px);
    @include size($w: 100%, $h: 52px);

    @include media-tablet {
      @include hidden;
    }
  }

  &__navbar {
    @include border(orange);
    @include hidden;
    @include position(sticky, $top: 0);
    @include padding($right: 12px, $left: 12px);
    @include size($h: 100vh);

    @include media-tablet {
      @include block;
    }
  }

  &__main {
    @include media-tablet {
      @include border($offwhite-line, right left);
    }
  }

  &__title {
    @include border(red);
    @include hidden;
    @include position(sticky, $top: 0);
    @include padding($right: 16px, $left: 16px);
    @include size($h: 52px);

    @include media-tablet {
      @include block;
    }
  }

  &__content {
    height: 500vh;
    @include border(blue);
    @include padding($top: 52px, $right: 16px, $bottom: 52px, $left: 16px);

    @include media-tablet {
      @include padding($top: 0, $bottom: 0);
    }

    > div {
      @include flex(column, space-between);
      @include size($h: 100%);
    }
  }

  &__aside {
    @include hidden;

    @include media-laptop {
      @include block;
    }
  }

  &__searchbar {
    @include border(green);
    @include position(fixed, $top: 0);
    @include padding($left: 20px, $right: 12px);
    @include size($w: 320px, $h: 52px);
    @include z-index(1);

    @include media-desktop {
      @include padding($left: 30px);
      @include size($w: 390px);
    }
  }

  &__sidebar {
    height: 150vh;
    @include border(magenta);
    @include padding($top: 52px, $left: 20px, $right: 12px);
    @include size($w: 320px);

    @include media-desktop {
      @include padding($left: 30px);
      @include size($w: 390px);
    }

    > div {
      @include flex(column, space-between);
      @include size($h: 100%);
    }
  }
}
