@mixin border($color, $positions: null) {
  @if $positions {
    @each $position in $positions {
      border-#{$position}: 1px solid $color;
    }
  } @else {
    border: 1px solid $color;
  }
}

@mixin size($w: null, $h: null, $min-w: null, $min-h: null, $max-w: null, $max-h: null) {
  width: $w;
  height: $h;
  min-width: $min-w;
  min-height: $min-h;
  max-width: $max-w;
  max-height: $max-h;
}

@mixin padding($top: null, $right: null, $bottom: null, $left: null) {
  padding-top: $top;
  padding-right: $right;
  padding-bottom: $bottom;
  padding-left: $left;
}

@mixin margin($top: null, $right: null, $bottom: null, $left: null) {
  margin-top: $top;
  margin-right: $right;
  margin-bottom: $bottom;
  margin-left: $left;
}

@mixin z-index($index) {
  z-index: $index;
}
