@mixin position($position, $top: null, $bottom: null, $left: null, $right: null) {
  position: $position;
  top: $top;
  bottom: $bottom;
  left: $left;
  right: $right;
}

@mixin grid($justify: null, $align: null) {
  display: grid;
  justify-content: $justify;
  align-content: $align;
}

@mixin grid-cols($cols) {
  grid-template-columns: $cols;
}

@mixin flex($direction: null, $justify: null, $align: null) {
  display: flex;
  flex-direction: $direction;
  justify-content: $justify;
  align-items: $align;
}
