// 100%(16+content+16)
$breakpoint-md: 500px;
// 88(12+64+12) + 100%(16+content+16)
$breakpoint-lg: 1008px;
// 88(12+64+12) + 920(600(16+content+16+2)+320(20+288+12))
$breakpoint-xl: 1265px;
// 275(12+251+12) + 990(600(16+content+16+2)+390(30+348+12))

@mixin media-tablet {
  // 500~1007
  @media (min-width: $breakpoint-md) {
    @content;
  }
}

@mixin media-laptop {
  // 1008~1264
  @media (min-width: $breakpoint-lg) {
    @content;
  }
}

@mixin media-desktop {
  // 1265~
  @media (min-width: $breakpoint-xl) {
    @content;
  }
}
